.contactInfoWrapper{
    padding:0 2rem;
}

.contactText{
    line-height: 1.8rem;
    text-align: justify;
    letter-spacing: 1px;
}

.map{
    width: 100%;
    height: 30rem;
    padding: 3rem 5rem;
    position: relative;
}

.mapContainer {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}