@font-face {
  font-family: 'Nobel';
  font-style: normal;
  font-weight: normal;
  src: local('Nobel Book'), url('./fonts/nobel/Nobel\ Book.woff') format('woff');
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-size: 18px;
  box-sizing: border-box;
  font-family: 'Nobel';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.banner{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  background: #e2e2e2;
  margin: 0 5rem;
}

.banner>p{
  font-size: 2rem;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
}

.marTop{
  margin-top: 6rem;
}

.whiteTxt{
  color: white;
}

.imgMain{
  background-image: url("./imgs/books.jpg");
}

.imgServices{
  background-image: url("./imgs/services.jpg");
}

.imgAbout{
  background-image: url("./imgs/about.jpg");
}