.serviceCard{
    background: #939ca0b5;
    margin: 1rem;
    transition: all 0.5s ease;
    padding: 5rem 2rem;
    cursor: pointer;
}

@media all and (max-width: 1280px) {
    .serviceCard{
        background: #939ca0b5;
        margin: 1rem;
        transition: all 0.5s ease;
        padding: 4rem 2rem;
        cursor: pointer;
        height: 6rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.serviceCard:hover{
    background: #ccc;
}

.serviceCard p{
    font-size: 1.2rem;
}
