.footerContainer{
    padding: 2rem 5rem;
    margin-top: 10rem;
    width: 100%;
    height: auto;
    display: flex;
    background: #1E272B;
    color: white;
}

.footerLogo{
    width: 45%;
    cursor: pointer;
}

.footerContainer>div{
    width: 25%;
    padding: 1rem;
    text-align: center;
}

.footerContainer ul{
    list-style: none;
    text-align: left;
    display: inline-block;
    font-size: 0.8rem;
    text-transform: uppercase;
}

.footerContainer ul>li:first-child{
    padding: 0.5rem 0
}

.footerContainer ul>li{
    padding: 0.3rem 0
}