.containerBox{
    padding: 3rem 5rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.joinBoxLeft{
    background-color: #0B4A65;
    padding: 1rem;
    color: white;
    margin-right: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
}

.joinBoxLeft p:first-child{
    font-size: 2rem;
    padding: 2rem 2rem 1rem 2rem;
}

.joinBoxLeft p:last-child{
    padding: 1rem 3rem 2rem 2rem;
    letter-spacing: 0.1rem;
    line-height: 1.5rem;
}

@media all and (max-width: 1280px) {
    .joinBoxLeft p:first-child{
        font-size: 1.5rem;
        padding: 1rem 1rem 0.5rem 1rem;
    }
    
    .joinBoxLeft p:last-child{
        padding: 0.5rem 1rem 1rem 1rem;
        
    }
}

.formStyle{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.formInput{
    width: 100%;
    padding: 1rem;
    margin-bottom: 1rem;
}

.formBtn{
    background: #1E272B;
    color: white;
    border: none;
    padding: 1rem 3rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.formBtn:hover{
    background-color: #aaa;
    color: black;
}