.hText{
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
    margin-bottom: 1.5rem;
    text-transform: uppercase;
}

.wwaWrapper{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    padding: 5rem 10rem;
}

@media all and (max-width: 1280px) {
    .wwaWrapper{
        display: flex;
        flex-direction: row;
        width: 100%;
        height: auto;
        padding: 5rem 5rem;
    }
    .wwaCol{
        width: 50%;
        height: 100%;
        padding: 0 2rem;
    }
}

.wwaCol{
    width: 50%;
    height: 100%;
    padding: 0 3rem;
}

.valuesContainer{
    list-style: none;
    padding-top: 0.8rem;
}

.pText{
    line-height: 1.8rem;
    text-align: justify;
    letter-spacing: 0.5px;
}

.valuesContainer>li{
    padding-bottom: 1rem;
    line-height: 1.5rem;
    text-align: justify;
    letter-spacing: 1px;
}

.subHeading{
    text-transform: uppercase;
    font-size: 1rem;
}