.teamContainer{
    margin-bottom: 5rem;
    display: flex;
}

.teamContainer>div{
    width: 25%;
    margin-right: 2rem;
}

.teamContainer>p {
    width: 70%;
}

.teamContainer img {
    width: 100%;
    height: auto;
}

.nameTag{
    background: #b0b0b0;
    padding: 1rem;
    text-transform: uppercase;
    letter-spacing: 5px;
}

.nameTag>span:first-child{
    font-weight: bold;
}